    text-align: center;
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    border-radius: var(--big-border-radius);
    padding: 5px;
    margin: 5px;
    border: var(--border-preset);
}

.sideBarContent {
    padding: 5px 0px 5px 0px;
    text-align: left;
    overflow: auto;
